}
html{
	font-size: 62.5%;
	color: #000;
	height:100%;
}

header{
	font-size: 1.8rem;
	font-family: $montserrat;
	nav{
		ul{
			list-style: none;
			text-align: center;
			padding: 40px 0;
		}
		li{
  			display: inline-block;
  			margin-right: 47px;
  			padding: 10px 0;
  			&:nth-child(2){
  				margin-right: 130px;
  			}
  			&:nth-child(3){
  				margin-left: 130px;
  			}
  			&:last-child{
  				margin: 0;
  			}
		}
		a{
			color: #000;
			height: 100%;
			text-decoration: none;
			position: relative;
			transition: width .5s ease, color .5s ease;

			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: -10px;
				height: 3px;
				width: 0;
				background: transparent;
				transition: width .5s ease, background-color .5s ease;
			}

			&:hover{
				color: #70a3d0;
				&:after{
					width: 100%;
					background: #70a3d0;
				}
			}
		}
	}
	.banner{
		position: relative;
		width: 100%;
		padding: 235px;
		background-image: url('../images/herobanner.jpg');
		background-size: cover;
		background-position: top center;
		background-repeat: no-repeat;
      	background-attachment: fixed;
      	.logo{
      		width: 150px;
      		position: absolute;
      		top: -40px;
      		left: calc(50% - 75px);
      	}
  		h1{
  			font-family: $libre;
  			font-size: 7.3rem;
  			font-style: italic;
  			text-align: center;
  			position: relative;
  			display: block;
  			z-index: 5;
  			&:after{
  				content: "";
  				display: block;
  				height: 53px;
  				width: 643px;
  				background-color: #fff;
  				position: absolute;
  				top: -14px;
  				left: 0;
  				z-index: -1;
  			}
  		}
	}
}
